import React from 'react';
import burger from "./burger_menu.svg";
import './burger.css'

class Burger extends React.Component{

        state={
            transform:'rotate(0deg)'
        }
        
        handleRotate = () => {
            this.setState({transform: 'rotate(90deg)'})
            
        }
        handleReset = () => {
            this.setState({transform: 'rotate(0deg)'})
        }
    
    render(){
      
        
        return(
            <ul className='ulmenu'>
                        <li className="dropdown" >
                        <img href='#dropdownbtn' className='dropbtn' src={burger} alt="Menu"/>
                        <div className="dropdown-content">
                        <a href="#one"> One</a>
                        <a href="#two"> Two</a>
                        <a href="#three"> Three</a>
                        <a href="#four"> Four</a>
                </div>
                </li>
                </ul>
        )
    }
}
export {Burger}
